<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">
        首页
      </el-breadcrumb-item>
      <el-breadcrumb-item>相册</el-breadcrumb-item>
      <el-breadcrumb-item>{{ albummsg.albumname || '相册名' }}</el-breadcrumb-item>
    </el-breadcrumb>
    <el-card>
      <el-row>
        <!-- <el-col :span="2">
          <div class="uploader-tou">
            <img
              :src="imageUrl"
              class="avatar"
            >
          </div>
        </el-col> -->
        <el-col :span="22">
          <h4>{{ username || '用户名' }}</h4>
          <div v-if="!user.status">
            <el-upload
              class="upload-demo"
              name="excelTermFile"
              :show-file-list="false"
              :on-success="success"
              :action="defUrl + 'uploadTermFile'"
              :data="{ id: albumid }"
            >
              <el-button
                type="success"
                size="mini"
                icon="el-icon-upload2"
              >
                上传照片
              </el-button>
            </el-upload>
          </div>
        </el-col>
      </el-row>
      <el-row>
        <ul class="albumlist">
          <li
            v-for="item in albummsg.photoList"
            :key="item.albumid"
          >
            <div class="albumlist-img">
              <el-image
                fit="contain"
                :src="item.photoban === 1 ? defUrl + item.photoaddress : ''"
                width="164px"
                height="164px"
                :preview-src-list="srcList"
              >
                <div
                  slot="error"
                  class="image-slot"
                  v-if="item.photoban === 1"
                >
                  <i class="el-icon-picture-outline" />
                </div>
                <div
                  slot="error"
                  class="image-slot"
                  v-if="item.photoban === 2"
                >
                  <i class="el-icon-warning" />
                </div>
              </el-image>
            </div>
            <div class="albumlist-b" v-if="!user.status">
              <form
                :action="`http://localhost:10086/ps/downloadFile?fileName=${item.photoaddress}`"
                method="post"
              >
                <el-input
                  size="mini"
                  icon="el-icon-download"
                  value="下载"
                  type="submit"
                />
              </form>
              <el-button
                type="danger"
                size="mini"
                icon="el-icon-delete"
                @click.prevent="del(item)"
              />
            </div>
          </li>
        </ul>
      </el-row>
    </el-card>
  </div>
</template>
<script>
export default {
  data () {
    return {
      user: JSON.parse(window.sessionStorage.user),
      username: JSON.parse(window.sessionStorage.user).username || JSON.parse(window.sessionStorage.user).adminname,
      albumid: this.$route.params.id,
      defUrl: 'http://localhost:10086/ps/',
      imageUrl: '',
      srcList: [],
      albummsg: {}
    }
  },
  created () {
    this.getphoto()
  },
  methods: {
    async getphoto () {
      const { data: res } = await this.$http.get('album/getphotobyid', { params: { id: this.albumid } })
      this.albummsg = res.obj
      this.albummsg.photoList.some(item => {
        const src = this.defUrl + item.photoaddress
        if (item.photoban === 1) {
          this.srcList.push(src)
        }
      })
    },
    async del (item) {
      // 弹窗询问是否删除
      const confirmres = await this.$confirm('是否确认删除该相片?', '确认删除', {
        confirmButtonText: '确认',
        cancelButtonText: '取消',
        type: 'warning'
      }).catch(err => err)
      if (confirmres != 'confirm') {
        return this.$message.info('已取消删除')
      }
      const { photoid: id } = item
      const { data: res } = await this.$http.get('user/delPhoto', { params: { id } })
      if (res.code === 200) {
        this.getphoto()
      }
    },
    // 下载图片
    async download (item) {
      const { photoaddress: fileName } = item
      const { data: res } = await this.$http.post('downloadFile', { fileName })
    },
    success () {
      console.log('123')
      this.getphoto()
    }
  }
}
</script>
<style lang="less" scoped>
.uploader-tou {
  width: 70px;
  height: 70px;
  box-sizing: border-box;
  overflow: hidden;
}
.avatar {
  width: 70px;
  height: 70px;
  display: block;
}
.upload-demo {
  width: 100px;
  height: 28px;
  display: inline-block;
  overflow: hidden;
}
.el-upload {
  text-align: left !important;
}
.albumlist {
  display: flex;
  list-style: none;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 0;
  margin-top: 20px;
  li {
    width: 174px;
    height: 220px;
    padding: 5px;
    margin-bottom: 10px;
    background-color: #fff;
    box-shadow: 1px 1px 10px #3332;
  }
}
li:not(:nth-child(7n)) {
  margin-right: 11px;
}
.albumlist-img {
  width: 100%;
  height: 168px;
  display: flex;
  justify-content: center;
  align-items: center;
  i {
    font-size: 40px;
  }
}
.albumlist-b {
  display: flex;
  height: 46px;
  align-items: center;
  justify-content: space-between;
}
a:hover,
a:focus {
  text-decoration: none;
}
</style>
