<template>
  <div>
    <!-- 面包屑 -->
    <el-breadcrumb separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">
        首页
      </el-breadcrumb-item>
      <el-breadcrumb-item>个人中心</el-breadcrumb-item>
    </el-breadcrumb>
    <el-row>
      <div class="usercenter">
        <el-col :span="5">
          <el-upload
            name="tou"
            class="uploader-tou"
            action=""
            :show-file-list="false"
            :on-success="handleAvatarSuccess1"
            :before-upload="beforeAvatarUpload1"
          >
            <img
              src="../assets/img/1.jpg"
              class="avatar"
            >
          </el-upload>
        </el-col>
        <el-col :span="19">
          <h4>{{ alterusermsgForm.username || '用户名' }}</h4>
          <p style="text-align: right;">
            <el-button
              size="mini"
              @click="dialogVisible = true"
            >
              修改
            </el-button>
          </p>
          <div class="usermsg">
            <label>性别</label>：
            <span>{{ alterusermsgForm.usersex ? (alterusermsgForm.usersex === 1 ? '男' : '女') : '空' }}</span>
          </div>
          <div class="usermsg">
            <label>手机</label>：
            <span>{{ alterusermsgForm.userphone || '空' }}</span>
          </div>
          <div class="usermsg">
            <label>邮箱</label>：
            <span>{{ alterusermsgForm.useremail || '空' }}</span>
          </div>
        </el-col>
      </div>
    </el-row>
    <el-dialog
      center
      :visible.sync="dialogVisible"
      :before-close="handleClose"
      title="修改资料"
      width="30%"
    >
      <el-form
        inline
        label-width="60px"
        :rules="rules"
        ref="alterusermsgFormref"
        :model="alterusermsgForm"
      >
        <el-form-item
          prop="useranme"
          label="用户名"
        >
          <el-input v-model="alterusermsgForm.username" />
        </el-form-item>
        <el-form-item
          prop="usersex"
          label="性别"
        >
          <el-select
            v-model="alterusermsgForm.usersex"
            placeholder="请选择"
          >
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            />
          </el-select>
        </el-form-item>
        <el-form-item label="密码">
          <el-input v-model="alterusermsgForm.userpassword" />
        </el-form-item>
        <el-form-item
          prop="userphone"
          label="手机"
        >
          <el-input v-model="alterusermsgForm.userphone" />
        </el-form-item>
        <el-form-item
          prop="useremail"
          label="邮箱"
        >
          <el-input v-model="alterusermsgForm.useremail" />
        </el-form-item>
      </el-form>
      <span
        slot="footer"
        class="dialog-footer"
      >
        <el-button @click="submit('alterusermsgFormref')">提交</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>
<script>
export default {
  data () {
    const validateemail = (rele, value, callback) => {
      const email = /^([a-zA-Z]|[0-9])(\w|\-)+@[a-zA-Z0-9]+\.([a-zA-Z]{2,4})$/
      if (email.test(value) || value == '' || value == undefined) {
        callback()
      } else {
        callback(new Error('请输入正确的邮箱'))
      }
    }
    const validatephone = (rele, value, callback) => {
      const phone = /^1[3456789]\d{9}$/
      if (phone.test(value) || value == '' || value == undefined) {
        callback()
      } else {
        callback(new Error('请输入正确的手机号码'))
      }
    }
    return {
      imageUrl: '../assets/img/1.jpg',
      dialogVisible: false,
      usermsg: JSON.parse(window.sessionStorage.user),
      alterusermsgForm: {},
      options: [
        { label: '男', value: 1 },
        { label: '女', value: 2 }
      ],
      rules: {
        username: [{ required: true, message: '请输入用户名', trigger: 'blur' }],
        tel: [{ validator: validatephone, trigger: 'change' }],
        email: [{ validator: validateemail, trigger: 'change' }]
      }
    }
  },
  created () {
    console.log(this.usermsg)
    if (this.usermsg.adminid) {
      this.alterusermsgForm = {
        username: this.usermsg.adminname,
        userpassword: this.usermsg.adminpassword
      }
      console.log(this.alterusermsgForm)
    } else {
      this.alterusermsgForm = this.usermsg
    }
  },
  methods: {
    // 提交修改信息
    submit (ref) {
      this.$refs[ref].validate(async isvalidate => {
        if (!isvalidate) return false
        delete this.alterusermsgForm.userban
        console.log(this.alterusermsgForm)
        const { data: res } = await this.$http.post('user/updateUser', this.alterusermsgForm)
        // 更新头部用户名
        this.resetSetItem('user', JSON.stringify(this.alterusermsgForm))
        this.handleClose()
        this.getusermsg()
      })
    },
    // 上传成功后
    handleAvatarSuccess1 (res, file) {
      // this.imageUrl = URL.createObjectURL(file.raw)
    },
    // 上传前
    beforeAvatarUpload1 (file) {
      const isJPG = file.type === 'image/jpeg'
      const isLt2M = file.size / 1024 / 1024 < 2

      if (!isJPG) {
        this.$message.error('上传头像图片只能是 JPG 格式!')
      }
      if (!isLt2M) {
        this.$message.error('上传头像图片大小不能超过 2MB!')
      }
      return isJPG && isLt2M
    },
    // 对话框关闭事件
    handleClose () {
      this.dialogVisible = false
    }
  }
}
</script>
<style lang="less" scoped>
.uploader-tou {
  width: 100px;
  height: 100px;
  box-sizing: border-box;
  overflow: hidden;
}
.avatar {
  width: 100px;
  height: 100px;
  display: block;
}
.usermsg {
  label {
    width: 70px;
    text-align: justify;
    text-align-last: justify;
  }
  span {
    margin-left: 45px;
  }
}
.el-form-item {
  width: 100%;
}
.usercenter {
  height: 500px;
  margin: 0 200px;
  padding: 20px;
  overflow: hidden;
  background-color: #fff;
}
</style>
